.handebag-countent{
    width: 300px;
    margin-left: 10px;
    position: relative;
}
.list{
    text-align: center;
    width: 250px;
    overflow: scroll;
    cursor: pointer;
    scroll-behavior: smooth;
   
    
}
.btn1{
    position: absolute;
    top:32px;
    right: 5px;
    font-size: 20px;
    color: #4C82EF;
}
.btn2{
    position: absolute;
    top:32px;
    right: 5px;
    font-size: 20px;
    margin-right: 20px;
    color: #4C82EF;
}
.handebag-countent h3{
    font-size: 14px;
    font-weight: 400;
    margin-bottom: 10px;
    color: #949494;
    margin-left: 10px;

}

.list::-webkit-scrollbar{
    display: none;
}

.list-ul{
    width: 100px;
    list-style-type: none;
    display: flex;
    gap: 10px;
   margin-left: 10px;

}

.list-ul li{
    border: 2px  solid #4C82EF;
    padding: 2px 5px;
    border-radius: 5px;
    font-weight:500 ;
    color:#4C82EF;
    font-size: 12px;
}

li:hover{
    background-color: blueviolet;
    color: white;
}

.bags{
    display: flex;
    flex-direction: column;
    width: 220px;
    gap: 10px;
    background-color: #EFF3FD;
    align-items: center;
    justify-content: center;
    border-radius: 10px;
    margin-top: 10px;
    margin-left: 10px;
}
.bag-logo{
    display: flex;
    background-color: white;
    width: 210px;
    height: 70px;
    align-items: center;
    justify-content: center;
    border-radius: 10px;
    margin-top: 5px;
}
 .image-content{
    display: flex;
    flex-direction: column;
    
} 

.image-content h3{
    font-size: 12px;
}
.image-content .h31{
    font-weight: 500;
    color: #000;
}

.bag-image img{
    border-radius: 10px;
    margin-left: 5px;
}
.image-contents p{
    font-size: 12px;
    padding: 0px 10px;
}
.time{
    margin-left: 10px;
    margin-top: 5px;
}
.filiters li{
    font-size: 12px;
    list-style-type: none;
    display: inline-block;
    margin: 3px 5px;
    border: 2px solid #ccc ;
    padding: 2px 5px;
    font-weight: 500;
    border-radius: 5px;
    color: #ccccccdf;
    margin-top: 5px;
}
.filiters li:hover{
    border: 2px solid  #4C82EF;
    color: #4C82EF;
    background-color: white;
    cursor: pointer;
}
.select-filter{
    margin:5px 0px;
    color: #ccc;
    margin-left: 5px;
}
