.footer-container{
    background-color: #EFF3FD;
    width: 320px;
    height: 80px;
    border-radius: 0px 0px 20px 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin: 5px auto;
    
   
}
.footer-content{
   display: flex;
   justify-content: center;
   align-items: center;
   
}
.input{
    width: 100%;
    display: flex;
    gap: 10px;
    margin-top: 15px;
}

.input input{
    width: 100%;
    margin-left: 10px;
    border-radius: 5px;
    outline: none;
    border: none;
    padding-left: 10px;

}
.input button{
    padding: 5px 25px;
    margin-right: 10px;
    background-color:#3374f7;
    border: none;
    border-radius: 10px;
    font-size: 18px;
    color: white;
    font-weight: 800;
}
.footer-content p{
    font-size: 14px;

}

.footer-content span{
    color: #a8a0a0 ;
    font-weight: 700;
}

.filter{
    position: absolute;
    top: 73%;
    right: 20px;
    color: #898989;
    display: flex;
    font-size:20px ;
    align-items: center;
    gap: 5px;
}
.filter p{
     font-size: 12px;
}
