
.head-container {
    background-color: #EFF3FD;
    width: 320px;
    height: 70px;
    border-radius: 20px 20px 0px 0px;
    margin: 5px auto;
    display: flex;
    justify-content: center;
    align-items: center;

}

.image img{
    margin-top: 5px;
    margin-right: 5px;
}




.head-container .image {
    width: 50px;
    height: 50px;
    background-color: #fff;
    border-radius: 50%;
    overflow: hidden;
    margin-left: 15px;
    
}

.dot {
    width: 8px;
    height: 8px;
    background-color: orange;
    border-radius: 50%;
    position: absolute;
    top: 55px;
    left: 55px;
    z-index: 1;

}

.head-detalis h2 {
    font-size: 14px;
    color: #4C82EF;

}

.chat {
    font-weight: 500;
}

.image-detalis {
    width: 80%;
    height: 100%;
    display: flex;
    gap: 12px;
    align-items: center;
}

.dot-online {

    width: 8px;
    height: 8px;
    background-color: orange;
    border-radius: 50%;
    position: absolute;
    top: 46px;
    right: 75px;

}

.online h2 {

    font-size: 14px;
    font-weight: 500;
    color: #4C82EF;
    margin-right: 20px;
    margin-top: 20px;
}
